import {gsap, TimelineMax as Timeline, Power1 } from 'gsap';
gsap.registerPlugin(Timeline, Power1);

const getPageTransitionTimeline = (node, delay) => {
  const timeline = new Timeline({ paused: true });
  timeline
    .from(node, {duration: delay, autoAlpha: 0, ease: Power1.easeIn })
    .from('h2', {duration: delay, autoAlpha: 0, ease: Power1.easeIn })
    .from('h1', {duration: delay, autoAlpha: 0, ease: Power1.easeIn })
    .from('h3', {duration: delay, autoAlpha: 0, ease: Power1.easeIn });

  var chips = document.querySelectorAll('.e-chip');
  for(var i=0; i<chips.length; i++){
    timeline.from(chips[i], {duration: delay/2, autoAlpha: 0, ease: Power1.easeIn }, "+=0");
  }


  return timeline;
}

export const play = (pathname, node, appears) => {
  const delay = 0.4
  let timeline

  if(pathname == '/'){
    document.body.className='emotions';
  }

  timeline = getPageTransitionTimeline(node, delay);
  timeline.play();  
}

export const exit = (node) => {
  const timeline = new Timeline({ paused: true });

  timeline.to(node, 0.4, { autoAlpha: 0, ease: Power1.easeOut });
  timeline.play();
}
